@import '../../../shared/variables.scss';
.tech {
  @extend %fonts;
  @include adaptive(padding-top, 146, 28);
  @include adaptive(padding-bottom, 90, 40);
  box-sizing: border-box;
  position: relative;
  &::before {
    content: '';
    position: absolute;
    transform: rotate(37.27deg);
    @include adaptive(width, 400, 90);
    @include adaptive(height, 400, 90);
    border-radius: 50%;
    @include adaptive(left, -200, -50);
    bottom: 0;
    z-index: -1;
    background-image: url('../../../shared/images/balls/ball-3.png');
    background-repeat: no-repeat;
    background-size: contain;
  }
  @media screen and (min-width: $_768) {
    &::after {
      content: '';
      position: absolute;
      transform: rotate(37.27deg);
      @include adaptive(width, 200, 90);
      @include adaptive(height, 200, 90);
      border-radius: 50%;
      @include adaptive(right, -200, -50);
      top: 0;
      z-index: -1;
      background-image: url('../../../shared/images/balls/ball-7.png');
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
}
.list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(286px, 1fr));
  justify-content: space-between;
  row-gap: 20px;
  column-gap: 20px;
  @include adaptive(margin-bottom, 100, 50);
  letter-spacing: 0.03em;
}
.item {
  letter-spacing: 0.03em;
  background: rgba(255, 255, 255, 0.02);

  border-radius: 2px;
  padding: 45px 38px;
  @media screen and (max-width: $_768) {
    padding: 27px;
  }
}
.itemTitle {
  @extend %fz-24-30;
  color: $mainColor;
  font-weight: 600;
  text-transform: uppercase;
  @include adaptive(margin-bottom, 30, 18);
}
.sublist {
  list-style-type: none;
}

.subitem {
  @include adaptive(margin-bottom, 17, 11);
  display: flex;
  align-items: center;
  @include adaptive(column-gap, 20, 10);
  justify-content: flex-start;
}
.image {
  @include adaptive(width, 32, 20);
}

.subtitle {
  @extend %fz-24-38;
  color: $secondaryColor;
  text-transform: capitalize;
}
